<template>
  <CRow class="justify-content-center">
    <CCol>
      <CCard>
        <CCardHeader class="d-flex justify-content-between">
          <div>
            <slot name="header">
              <CIcon name="cil-healing" />
              <strong>Users</strong>
            </slot>
          </div>
          <CButton variant="outline" color="success" @click="openModalNewMed()">
            <CIcon name="cil-plus" class="mt-0" />&nbsp;Add New User
          </CButton>
        </CCardHeader>
        <CCardBody>
          <v-client-table :data="rows" :columns="columns" :options="options">
            <div slot="actions" slot-scope="{ row }">
              <!-- <CButton
                class="mr-3"
                variant="outline"
                @click="openModalEdit(row)"
                color="primary"
              >
                Edit
                <CIcon name="cil-pencil" />
              </CButton> -->
              <CButton
                class="mr-3"
                variant="outline"
                @click="deleteRow(row)"
                color="danger"
              >
                Delete
                <CIcon name="cil-delete" />
              </CButton>
            </div>
          </v-client-table>
        </CCardBody>
      </CCard>
    </CCol>
    <sweet-modal :blocking="Boolean(true)" ref="modal" overlay-theme="dark">
      <CCard style="text-align: left; border: 1px solid">
        <CCardHeader>
          <strong>{{ formName }}</strong>
        </CCardHeader>
        <CForm @submit.prevent="submit">
          <CCardBody>
            <p
              v-for="(error, index) in errors"
              :key="index"
              class="alert alert-danger"
            >
              {{ error }}
            </p>
            <CRow>
              <CCol md="6">
                <CInput
                  label="First name"
                  :class="{ 'form-group--error': $v.fname.$error }"
                  v-model.trim="$v.fname.$model"
                />
                <div class="error" v-if="!$v.fname.required">
                  First name is required
                </div>
              </CCol>
              <CCol md="6">
                <CInput
                  label="Last name"
                  :class="{ 'form-group--error': $v.lname.$error }"
                  v-model.trim="$v.lname.$model"
                />
                <div class="error" v-if="!$v.lname.required">
                  Last name is required
                </div>
              </CCol>
            </CRow>
            <CInput
              label="NIC No"
              :class="{ 'form-group--error': $v.nic.$error }"
              v-model.trim="$v.nic.$model"
            />
            <div class="error" v-if="!$v.nic.required">NIC is required</div>
            <CRow>
              <CCol md="6">
                <CInput
                  label="Email"
                  :class="{ 'form-group--error': $v.email.$error }"
                  v-model.trim="$v.email.$model"
                  type="email"
                />
                <div class="error" v-if="!$v.email.required">
                  Email is required
                </div>
                <div class="error" v-if="!$v.email.email">
                  Please provide a valid email
                </div>
              </CCol>
              <CCol md="6">
                <CInput
                  label="Phone Number"
                  :class="{ 'form-group--error': $v.phone.$error }"
                  v-model.trim="$v.phone.$model"
                  type="phone"
                />
                <div class="error" v-if="!$v.phone.required">
                  Phone number is required
                </div>
              </CCol>
            </CRow>
            <CInput
              label="Password"
              v-model.trim="$v.password.$model"
              v-if="!isEdit"
              :class="{ 'form-group--error': $v.password.$error }"
            />
            <div class="error" v-if="!$v.password.required">
              Password is required
            </div>
            <CRow>
              <CCol md="6">
                <CInput
                  label="Date of Birth"
                  :class="{ 'form-group--error': $v.dob.$error }"
                  v-model.trim="$v.dob.$model"
                  type="date"
                />
                <div class="error" v-if="!$v.dob.required">
                  Date of birth is required
                </div>
              </CCol>
              <CCol md="6">
                <CInput
                  label="Joined Date"
                  :class="{ 'form-group--error': $v.joined_date.$error }"
                  v-model.trim="$v.joined_date.$model"
                  type="date"
                />
                <div class="error" v-if="!$v.joined_date.required">
                  Joined date is required
                </div>
              </CCol>
            </CRow>
            <CInput
              label="Address"
              :class="{ 'form-group--error': $v.address.$error }"
              v-model.trim="$v.address.$model"
            />
            <div class="error" v-if="!$v.address.required">
              Address is required
            </div>
          </CCardBody>
          <CCardFooter>
            <CButton class="col" type="submit" variant="outline" color="success">
              <CIcon name="cil-check-circle" />&nbsp;Submit
            </CButton>
          </CCardFooter>
        </CForm>
      </CCard>
    </sweet-modal>
  </CRow>
</template>
<script>
import moment from "moment";
import { required, email } from "vuelidate/lib/validators";
export default {
  name: "Users",
  data() {
    return {
      formName: "",
      email: "",
      phone: "",
      fname: "",
      lname: "",
      password: "",
      dob: "",
      nic: "",
      joined_date: "",
      address: "",
      id: "",
      isEdit: false,
      rows: [],
      errors: [],
      columns: [
        "name",
        "email",
        "phone",
        "nic",
        "joined_date",
        "user_type",
        "actions",
      ],
      options: {
        columnsClasses: {
          actions: "text-center",
          joined_date: "text-center",
          user_type: "text-center",
          nic: "text-center",
        },
        filterByColumn: true,
        perPage: 10,
        texts: {
          filter: "Filter:",
          filterBy: "Filter by {column}",
          count: " ",
        },
        filterable: ["name"],
        headings: {
          user_type: "User Level",
          nic: "NIC",
        },
      },
    };
  },
  validations: {
    email: {
      required,
      email,
    },
    fname: { required },
    phone: { required },
    lname: { required },
    password: { required },
    dob: { required },
    nic: { required },
    joined_date: { required },
    address: { required },
  },
  created() {
    this.getData();
  },
  methods: {
    submit() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.errors.push("Please check the form and submit.");
      } else {
        this.formData = {
          firstname: this.fname,
          lastname: this.lname,
          email: this.email,
          phone: this.phone,
          password: this.password,
          dob: this.dob,
          joined_date: this.joined_date,
          address: this.address,
          nic: this.nic,
        };
        if (this.isEdit === false) {
          this.$http
            .post("http://localhost:3000/users/post", this.formData)
            .then((res) => {
              this.$swal.fire("Done!", "User created successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        } else {
          this.$http
            .put("http://localhost:3000/users/" + this.id, this.formData)
            .then((res) => {
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        }
        this.$refs.modal.close("modal");
      }
    },
    getData() {
      this.$http
        .get("http://localhost:3000/users/get")
        .then((res) => {
          res.data.forEach((item) => {
            let local = moment.utc(item.dob).local().format("YYYY-MM-DD");
            item.dob = local;
            let local1 = moment
              .utc(item.joined_date)
              .local()
              .format("YYYY-MM-DD");
            item.joined_date = local1;
          });
          this.rows = res.data;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    deleteRow(row) {
      this.$http
        .delete("http://localhost:3000/user/" + row._id)
        .then((res) => {
          this.getData();
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    async openModalNewMed() {
      this.isEdit = false;
      this.$refs.modal.open("modal");
      this.formName = "New User Form";
      this.fname = "";
      this.lname = "";
      this.email = "";
      this.phone = "";
      this.nic = "";
      this.password = "";
      this.dob = "";
      this.joined_date = "";
      this.address = "";
      this.errors = []
    },
    async openModalEdit(row) {
      console.log(row);
      this.isEdit = true;
      this.id = row._id;
      this.$refs.modal.open("modal");
      this.formName = "Edit User Form";
      this.fname = row.firstname;
      this.lname = row.lastname;
      this.email = row.email;
      this.nic = row.nic;
      this.phone = row.phone;
      this.password = row.password;
      this.dob = row.dob;
      this.joined_date = row.joined_date;
      this.address = row.address;
      this.errors = []
    },
  },
};
</script>